import {Component, ViewChild} from "@angular/core";
import {Storage} from "@ionic/storage";
import {Platform, IonicApp, Nav, ModalController, Keyboard, ToastController, Events} from "ionic-angular";
import {NativeService} from "../providers/NativeService";
import {TabsPage} from "../pages/tabs/tabs";
import {LoginInfo} from "../model/UserInfo";
import {LoginPage} from "../pages/login/login";
import {Helper} from "../providers/Helper";
import {ENABLE_FUNDEBUG} from "../providers/Constants";
import {GlobalData} from "../providers/GlobalData";
import {CommonService} from "../service/CommonService";
declare var fundebug;

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild('myNav') nav: Nav;
  rootPage = TabsPage;
  backButtonPressed: boolean = false;

  constructor(private platform: Platform,
              private keyboard: Keyboard,
              private ionicApp: IonicApp,
              private storage: Storage,
              private globalData: GlobalData,
              private helper: Helper,
              private toastCtrl: ToastController,
              private modalCtrl: ModalController,
              private events: Events,
              private commonService: CommonService,
              private nativeService: NativeService) {
    platform.ready().then(() => {
      if (ENABLE_FUNDEBUG && this.nativeService.isMobile()) {//app
        this.nativeService.getVersionNumber().subscribe(version => {
          fundebug.appversion = version;
        })
      }
      this.helper.initJpush();//
      this.storage.get('LoginInfo').then((loginInfo: LoginInfo) => {
        if (loginInfo) {
          this.globalData.token = loginInfo.access_token;
          this.globalData.showLoading = false;
          this.commonService.getNewToken().subscribe(res => {
            this.globalData.token = res.access_token;
            this.events.publish('user:login', loginInfo);
          })
        } else {
          let modal = this.modalCtrl.create(LoginPage);
          modal.present();
          modal.onDidDismiss(data => {
            data && console.log(data);
          });
        }
      });
      this.nativeService.statusBarStyleDefault();
      this.nativeService.splashScreenHide();
      this.registerBackButtonAction();//
      this.assertNetwork();//
      this.helper.assertUpgrade().subscribe(res => {//app
        res.update && this.nativeService.downloadApp();
      });
      this.tokenHandle();//token
      this.nativeService.sync();//app
    });
  }

  assertNetwork() {
    if (!this.nativeService.isConnecting()) {
      this.toastCtrl.create({
        message: ',',
        showCloseButton: true,
        closeButtonText: ''
      }).present();
    }
  }

  registerBackButtonAction() {
    if (!this.nativeService.isAndroid()) {
      return;
    }
    this.platform.registerBackButtonAction(() => {
      if (this.keyboard.isOpen()) {//
        this.keyboard.close();
        return;
      }
      //toastloadingOverlay
      // this.ionicApp._toastPortal.getActive() ||this.ionicApp._loadingPortal.getActive()|| this.ionicApp._overlayPortal.getActive()
      let activePortal = this.ionicApp._modalPortal.getActive() || this.ionicApp._toastPortal.getActive() || this.ionicApp._overlayPortal.getActive();
      if (activePortal) {
        activePortal.dismiss();
        return;
      }
      let activeVC = this.nav.getActive();
      let tabs = activeVC.instance.tabs;
      let activeNav = tabs.getSelected();
      return activeNav.canGoBack() ? activeNav.pop() : this.nativeService.minimize();//this.showExit()

    }, 1);
  }

  //
  showExit() {
    if (this.backButtonPressed) { //true2APP
      this.platform.exitApp();
    } else {
      this.nativeService.showToast('');
      this.backButtonPressed = true;
      setTimeout(() => { //2false
        this.backButtonPressed = false;
      }, 2000)
    }
  }

  //token
  tokenHandle() {
    let timer = this.helper.timerRefreshToken();//token
    this.platform.pause.subscribe(() => {//app
      clearInterval(timer);
    });
    this.platform.resume.subscribe(() => {//app
      timer = this.helper.timerRefreshToken();
      this.commonService.getNewToken().subscribe(res => {
        this.globalData.token = res.access_token;
      })
    });
  }


}



